import React, { useState, useReducer, useEffect, useCallback, useMemo } from 'react';
import { FaAngleLeft, FaAngleRight } from 'react-icons/fa';

interface ScrollProps {
	ref?: null | HTMLElement
}

export default function useScroll(props: ScrollProps = {}) {

	const [stage, setStage] = useState(props.ref);
	useEffect(() => {
		if (!props.ref) return;
		setStage(props.ref);
	}, [setStage, props.ref]);

	const reduceScroll = useCallback(() => {
		if (!stage) return [false, true];
		let l = false, r = false;
		if (stage.scrollLeft > 10) l = true;
		if (stage.scrollLeft + stage.offsetWidth < stage.scrollWidth - 10) r = true;
		return [l, r];
	}, [stage]);
	const [canScroll, updateScroll] = useReducer(reduceScroll, [false, true]);
	useEffect(() => {
		if (!stage) return;
		updateScroll();
		stage.addEventListener('scroll', updateScroll);
		return () => stage.removeEventListener('scroll', updateScroll);
	}, [stage, updateScroll]);

	const scroll = useCallback((direction: 'left' | 'right') => {
		if (!stage) return;
		const amount = 100;
		stage.scrollBy({
			left: amount * (direction === 'left' ? -1 : 1),
			behavior: 'smooth'
		});
	}, [stage]);

	const ScrollComponents = useCallback(() => {
		return <>
			<div className='scroll' id='scroll-left' onClick={() => scroll('left')} style={{ opacity: canScroll[0] ? undefined : 0}}>
				<FaAngleLeft />
			</div>
			<div className='scroll' id='scroll-right' onClick={() => scroll('right')} style={{ opacity: canScroll[1] ? undefined : 0}}>
				<FaAngleRight />
			</div>
		</>;
	}, [scroll, canScroll]);

	return { canScroll, setStage, scroll, ScrollComponents };

}