import React, { useState, useEffect, useCallback, useMemo, useRef, RefObject } from 'react';
import cx from 'classnames';
import { StaticContext, Redirect } from 'react-router';
import { RouteComponentProps, Route, Link } from 'react-router-dom';
import { GET } from '../utils/requests';
import { SingleGame } from '../chess/ChessCom';
import styles from './css/game.module.css';
import chessStyles from '../css/chesscom.module.css';
import { Player } from '../models';
import { RawResult } from '../interfaces';
import { Game as ResultGame } from '../resources/result';
import { px } from '../utils/prototype';

export default function Game(props: RouteComponentProps<any, StaticContext, any>) {

	const id = props.match.params.id as string;
	const [result, setResult] = useState({} as RawResult);
	const updateResult = useCallback(() => GET({
		url: px('result', id)
	}).then(setResult), [id, setResult]);

	// Retrive the results on page load (includes PGN of all games for the match)
	useEffect(() => {
		updateResult();
	}, [updateResult]);

	// Extract games from RawResult interface (into array of objects)
	const games = useMemo(() => Object.values(result.games || {}), [result.games]);

	// Handle game list scroll behaviour
	const more = useRef() as RefObject<HTMLDivElement>;
	const scroll = useCallback((direction: 'up' | 'down') => {
		let div = more.current;
		if (!div) return;
		let width = div.getBoundingClientRect().width + 100;
		div.scrollBy({
			top: width * (direction === 'up' ? -1 : 1),
			behavior: 'smooth'
		});
	}, [more]);

	// Retrieve list of players (teams) if tournament is played on chesscom or lichess
	const [players, setPlayers] = useState([] as Player[]);
	const [team, setTeam] = useState(undefined as undefined | boolean);
	const updatePlayers = useCallback(() => {
		let { idW, idB, tournamentId } = result;
		if (!idW || !idB) return;
		if (result.chessComTeamLink || result.lichessTeamLink) setTeam(true);
		else if (result.chessComLink || result.lichessLink) setTeam(false);
		GET({
			url: px('tournament', tournamentId, 'fetchPlayers'),
			params: {
				ids: [idW, idB].join(',')
			}
		}).then(setPlayers);
	}, [result]);

	useEffect(() => {
		updatePlayers();
	}, [updatePlayers]);

	const sorted = useMemo(() => {
		return games.sort((a, b) => {
			if (a.board !== b.board) return a.board - b.board;
			if (a.game !== b.game) return a.game - b.game;
			return 0;
		});
	}, [games]);

	return (
		<div id="main">
			<div className={[styles.stage].join(' ')}>

				{/* Redirect for no game path */}
				<Route path='/g/:id' render={(rProps) => {
					if (!result) return null;
					return <Redirect to={{
						pathname: px('g', rProps.match.params.id, 1),
						search: rProps.location.search,
						state: rProps.history.location.state
					}}/>;
				}} />
				
				{/* Main game viewer */}
				<div className={[styles.viewer, chessStyles.wrapper].join(' ')}>
					<div className={chessStyles.gameViewWrapper}>
						<Route path='/g/:id/:index' render={(rProps) => <DisplayGame {...rProps} games={games} />} />
					</div>
				</div>

				{/* List of games */}
				<div className={styles.rightCol}>
					<div className={[styles.more].join(' ')} ref={more}>
						{sorted.map((r, i) => 
							<Link key={cx('gameLink', i)} to={'/g/' + id + '/' + (i + 1).toString()} className={styles.gamelink}> 
								<span className={styles.linktext}>
									{`${r.board+1}-${r.game}     ${r.w.username} - ${r.b.username}`}
								</span>
							</Link>)}
					</div>
				</div>
			</div>
		</div>
	);

}

// Main interactive game component
function DisplayGame(props: RouteComponentProps<any, StaticContext, any> & {
	games: ResultGame[]
}) {
	const index = Number(props.match.params.index) - 1;
	useEffect(() => {
		setTimeout(() => window.dispatchEvent(new Event('resize')), 100);
	}, []);

	if (!props.games[index]) return null;
	return <SingleGame g={props.games[index]} />;
}