import React from 'react';
import PropTypes from 'prop-types';
import { months } from './constants';
import styles from './pgnviewer.module.css';
import cx from 'classnames';

class BoardHeader extends React.Component {
  render() {
    const { headerInfo } = this.props

    if (!headerInfo) return null

	  const link = headerInfo.Link;
    const whiteName = headerInfo.White
    const blackName = headerInfo.Black
    const whiteTeam = headerInfo.WhiteTeam
    const blackTeam = headerInfo.BlackTeam
    const blackElo = headerInfo.BlackElo
    const whiteElo = headerInfo.WhiteElo
	  const round = headerInfo.Round
	  const game = headerInfo.Game
    const result = headerInfo.Result
    const event = headerInfo.Event
    const site = headerInfo.Site
    const date = headerInfo.Date.split('.')
    const textDate = date && `${date[2]} ${months[new Number(date[1]) - 1]} ${date[0]}`

    return (
      <div className={cx(styles.pgnHeaderStyles, 'pgnHeader')}>
        {/* Player names and result */}
        {link ? 
			    <a href={link} rel='noopener noreferrer' target='_blank' className={styles.link}>
            {whiteName} ({whiteElo}) - {blackName} ({blackElo})   {result}
          </a> :
			    <div className={styles.link}>
            {whiteName} ({whiteElo}) vs. {blackName} ({blackElo})
          </div>}
        
        {/* Player teams */}
        { whiteTeam || blackTeam ? <div className={styles.matchName}>{whiteTeam} vs. {blackTeam}</div> : null}

        {/* Supporting match information */}
        <div className={styles.matchinfo}>
          {event ? <div className={styles.infoleft}>{event}</div> : null}
          {site ? <div className={styles.infoleft}>{site}</div>: null}
          
          {textDate ? <div className={styles.infocenter}>{textDate}</div>: null}
          
          {round ? <div className={styles.inforight}>Round {round}</div>: null}
          {game ? <div className={styles.inforight}>Game {game}</div>: null}
        </div>

      </div>
    )
  }
}

BoardHeader.propTypes = {
  headerInfo: PropTypes.object.isRequired
}


export default BoardHeader
